.App-logo {
  height: 35px;
  width: 35px;
  border-radius: 50%;
  
}

.data{
  overflow-y: hidden;
  overflow-x: scroll;
  width :100%;
}


.sidebar{
  height: 100%;
}

.card-img{
  width: 200px;
  height: 300px;
  margin-left: 12px;
  margin-top: 12px;
  
}
.card:hover{
  cursor: pointer;
}
.card{
  width: 225px;
}
.about-img{
  max-width: 200px;
  max-height: 300px;
  --bs-gutter-x: 1.5rem;
  
  width: 100%;
  padding-right: calc(var(--bs-gutter-x) * .5);
  padding-left: calc(var(--bs-gutter-x) * .5);
  margin-right: auto;
  
}


.div{
  display: flex;
  justify-content: space-between;
}

@media only screen and (max-width: 770px) {
  .div{
    display:inline-grid;
    align-items: center;
    text-align: center;
    justify-items: end;
    width: 400px;
  }
  .frame{
    align-items: center;
    text-align: center;
  }
  .about-img{
    max-width: 200px;
    max-height: 300px;
    --bs-gutter-x: 1.5rem;
    
    width: 100%;
    padding-right: calc(var(--bs-gutter-x) * .5);
    padding-left: calc(var(--bs-gutter-x) * .5);
    margin-right: auto;
    margin-left: auto;
  }
 
  .details{
    margin-left: .5%;
    margin-top: 5%;
    text-align: left !important;
  }

}
@media only screen and (min-width: 770px) {
  .about-img{
    max-width: 200px;
    max-height: 300px;
    --bs-gutter-x: 1.5rem;
    
    width: 100%;
    padding-right: calc(var(--bs-gutter-x) * .5);
    padding-left: calc(var(--bs-gutter-x) * .5);
    margin-right: auto;
   
  }
  .frame{
   display: flex;
  }
 
  .details{
    margin-left: .5%;
    margin-top: 5%;
    text-align: left !important;
  }

}
@media only screen and (min-width:1200px){
  .frame{
   display: flex;
  }
  .about-img{
    align-items: start;
  }
 
  .details{
    margin-left: 2%;
    margin-top: 5%;
    text-align: left !important;
    
  }
}